 .form-group{
    position: relative;
    width: 100%;
 }

 .form-group input {
    border:1px solid #a2a2a2;
    border-radius: 10px;
    margin-left: -16px;
    padding: 15px;
    width: 100%;
    outline: none;
}

.form-group .icon{
    position: absolute;
    top: 30px;
    right: 135px;
}